@import url("CustomProperty.css");

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  clear: right;
}

.rollAndTimerContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.rollButton {
  justify-content: center;
  margin-top: 50px;
}

.list_container {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.team1 {
  color: var(--team1-color);
  flex-direction: row;
  white-space: nowrap;
}

.team2 {
  color: var(--team2-color);
  flex-direction: row-reverse;
  white-space: nowrap;
}

.List {
  display: flex;
  flex-direction: column;
  border: 1px solid transparent;
}

.left_margin {
  margin-left: 200px;
}

.right_margin {
  margin-right: 200px;
}

.rollButton {
  padding: 20px 20px;
  border-radius: 35px;
  background-color: transparent;
  color: white;
  border: 6px solid goldenrod;
  font-size: 40px;
  cursor: pointer;
  /* 트랜지션 설정 */
  white-space: pre-line;
}

/* @keyframes rainbowText {
    0% { color: hsl(20, 100%, 50%); }
    50% { color: hsl(60, 100%, 50%); }
    100% { color: hsl(20, 100%, 50%); }
  } */

@keyframes blink {

  0%,
  50%,
  100% {
    color: goldenrod;
    border: 6px solid goldenrod;
    /* 깜빡거릴 때의 색상 */
  }

  25%,
  75% {
    color: white;
    border: 6px solid white;
    /* 중간 상태에서 색상을 변경 */
  }

  100% {
    color: goldenrod;
    border: 6px solid goldenrod;
    /* 마지막에 원하는 색상으로 변경 */
  }
}

.rollButton:hover {
  /* animation: rainbowText 1s infinite; */
  color: goldenrod;
  animation: blink 0.3s ease-in-out;
}