@import url("RandomPicker.css");
@import url("CustomProperty.css");
/* 다른 CSS 파일을 import */

.title {
    font-size: 40px;
    min-width: 242px;
    display: inline-block;
    white-space: nowrap;
    overflow: hidden;
    margin: 0px 10px 0px;
}

.title_container {
    display: relative;
    text-align: center;
    /* 가운데 정렬 */
}

.header_container {
    display: flex;
    justify-content: space-between;
}

.team_header {
    font-size: xx-large;
    background-color: var(--transparent-background-color);
    padding: 5px 100px;

}

.rollAndTimerContainer {
    display: flex;
    align-items: center;
    flex-direction: column;
}



@keyframes blink {

    0%,
    25%,
    75%,
    100% {
        color: goldenrod;
        /* 깜빡거릴 때의 색상 */
    }

    12.5%,
    50%,
    87.5% {
        color: white;
        /* 중간 상태에서 색상을 변경 */
    }

    100% {
        color: goldenrod;
        /* 마지막에 원하는 색상으로 변경 */
    }
}

.rollButton:hover {
    /* animation: rainbowText 1s infinite; */
    color: goldenrod;
    animation: blink 0.2s;
}