button {
    font-family: 'KBO-Dia-Gothic_bold', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
}

.mainButtonContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
    margin-right: 100px;
    /* 여백 추가 */
}

.mainButtonContainer button {
    width: 90%;

    padding: 5px 50px;

    margin-top: 10px;
    margin-bottom: 50px;

    border: 5px outset darkslategray;
    border-radius: 10px;

    background-color: rgba(255, 255, 255, 0.5);
    color: #000000;

    font-size: 40px;
    font-display: left;
    text-align: left;

    cursor: pointer;

    /* 각 버튼의 좌우 여백 조절 */
}

/* 1. 날아오기 전 위치 트랜지션 */
.animated_fly_in {
    transform: translateX(-1000%);
}

/*  2. 날아오는 트랜지션 */
.animated_fly_in.visible {
    opacity: 1;
    transform: translateX(0);
}

/* 3. hover시 색상변환 트랜지션 */
.animated_shape {
    transition: background-color 0.3s ease-in-out, transform 0.25s ease;
}

/* 4. hover시 색상 지정 */
.animated_shape:hover {
    background-color: white;
    /* 각 버튼의 좌우 여백 조절 */
}

.sceneMovingButtonContainer {
    justify-content: space-around;
    align-items: center;
    /* 수직 정렬 중앙 맞춤 */
    margin-right: 20px;
    position: absolute;
    /* 오른쪽 배치 */
    right: 0;
    /* 오른쪽 여백 추가 */
}

.sceneMovingButtonContainer button {
    margin: 10px 10px;
    /* 각 버튼의 좌우 여백 조절 */
    display: inline-block;
    padding: 5px 5px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    background-color: white;
    color: black;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
}

.sceneButtonContainer button:hover {
    background-color: turquoise;
}