@import url("CustomProperty.css");

.iconIMG {
    width: 40px;
    height: 40px;
    margin: 1px;
}

.champion_Container {

    transition: filter 0.3s;
    /* 트랜지션 효과 적용 */
    cursor: pointer;
    width: 200px;
    filter: blur(70%) brightness(50%) !important;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* 수직 가운데 정렬 */
    background-color: var(--transparent-background-color);
    border: 2px solid transparent;
    outline: 1px solid black;
}

.champion_text {
    margin-right: 5px;
    margin-left: 5px;
    font-family: 'KBO-Dia-Gothic_light';
    font-size: 20px;
}

.champion_Container:hover {
    filter: brightness(160%);
    border: 2px solid white;
}

.clicked.team1 {
    background-color: var(--team1-color);
    color: black;
    font-weight: bold;
    /* 클릭된 상태일 때의 테두리 스타일 */
}

.clicked.team2 {
    background-color: var(--team2-color);
    color: black;
    font-weight: bold;
    /* 클릭된 상태일 때의 테두리 스타일 */
}

/* 이걸 몰라서 여태 헤메고 있었네 */
.banned {
    position: relative;
}

.banned.team1 {
    background-color: var(--team2-color);
    filter: brightness(70%) grayscale(80%);
}

.banned.team2 {
    background-color: var(--team1-color);
    filter: brightness(70%) grayscale(80%);

}