:root {
    --team1-color: hsl(180, 100%, 70%);
    --team2-color: hsl(0, 100%, 70%);
    --transparent-background-color: rgba(255, 255, 255, 0.25);
    /* 변수 정의 */
}

.blurred {
    pointer-events: none;
    cursor: not-allowed;
    position: relative;
}

.blurred::after {
    filter: none;
    display: flex;
    align-items: center;
    justify-content: center;
    content: "구현 중입니다";
    background-color: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(10px);
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    filter: brightness(100%);
    transform: translate(-50%, -50%);
    color: gray;
    font-size: 80px;
    white-space: nowrap;
    z-index: 1;
    /* 다른 요소 위에 표시되도록 순서 조정 */
}