@import url("CustomProperty.css");

.option-container {
    height: 100px;
    display: flex;
    flex-direction: row;
    /* 내부 요소들을 가로로 나열합니다. */
    align-items: center;
    /* 내부 요소들을 수직으로 가운데 정렬합니다. */
    justify-content: space-evenly;
    /* 내부 요소들을 수평으로 가운데 정렬합니다. */
}

.optionName {
    font-size: 30px;
}

/* 라디오 버튼 컨테이너 스타일 */
.optionButtons-container {
    display: flex;
    justify-content: space-around;
    width: 500px;
}

.button-container {
    display: flex;
    align-items: center;
    /* 수직 가운데 정렬 */
}

.button-container label {
    cursor: pointer;
    padding: 0 20px;
    /* 여백을 패딩으로 설정 */
    font-size: 25px;
    white-space: nowrap;
}

.button-container input[type="checkbox"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 30px;
    height: 30px;
    margin: 2px;
    background-color: rgba(255, 255, 255, 0.1);
    /* 체크 박스 테두리 스타일 및 색상 설정 */
    border-radius: 4px;
    /* 라운드된 모서리 */
    cursor: pointer;
    position: relative;
}

.button-container input[type="checkbox"]:checked {
    border: 2px solid #ffcc00;
}

/* 체크 박스의 체크 마크 스타일 */
.button-container input[type="checkbox"]:checked::after {
    content: '\2714';
    /* 체크 표시(체크 모양의 유니코드 문자) */
    font-size: 20px;
    /* 체크 표시의 크기 조절 */
    color: #ffcc00;
    /* 체크 표시의 색상 설정 */
    position: absolute;
    top: 50%;
    /* 수직 정렬을 위한 위치 설정 */
    left: 50%;
    /* 수평 정렬을 위한 위치 설정 */
    transform: translate(-50%, -50%);
    /* 중앙 정렬 */
}

#pickingNumberCustomField {
    /* 원하는 스타일을 지정 */
    width: 100px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f5f5f5;
    font-size: 16px;
    color: #333;
}