@import url("CustomProperty.css");

.description {
    font-size: 15px;
}

.ControlButtonContainer {
    display: flex;
    /* 컨테이너를 플렉스 박스로 설정 */
    align-items: center;
    /* 수직 가운데 정렬 */
    justify-content: center;
    /* 수평 가운데 정렬 */
    /* 컨테이너의 높이를 설정 */
    /* 컨테이너의 폭을 설정 */
}

.ChampionList {
    display: flex;
    flex-wrap: wrap;
    /* 요소들이 가로로 넘치면 줄 바꿈 */
    justify-content: flex-start;
    /* 요소들을 가로로 고르게 배치 */
    list-style: none;
    /* 목록 표시 기호 제거 */
}

.ChampionContainer {
    width: 5%;
    /* 아이템 크기 설정 */
    margin: 0px 10px;
    /* 아래 여백을 1px로 설정하여 아이템을 가깝게 배치 */
    cursor: pointer;
}

.loadingChampionIMG {
    box-shadow: 0px 0px 20px black;
    /* h-offset v-offset blur spread color inset */
    filter: brightness(66%);
    width: 100%;
    border: 1.5px solid rgba(255, 255, 255, 0.25);
    /* dotted
    dashed
    double
    groove
    ridge
    inset
    outset */
    margin: 6px;
}

.loadingChampionIMG:hover {

    filter: brightness(120%);
    border: 1.5px solid white;
}

.selected {

    filter: brightness(120%);
    border: 1.5px solid yellow;
    /* You can change this color to your preferred highlight color */
    margin: 6px;
}

button {
    margin: 20px 0px;
    cursor: pointer;
}