@font-face {
  font-family: 'KBO-Dia-Gothic_bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-2@1.0/KBO-Dia-Gothic_bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'KBO-Dia-Gothic_light';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-2@1.0/KBO-Dia-Gothic_light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

body {
  font-family: 'KBO-Dia-Gothic_bold', 'KBO-Dia-Gothic_light', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  margin: 0;
  padding: 0;
  position: relative;
  /* 배경 이미지 위에 오버레이를 추가하기 위해 */
  background-image: url('https://raw.communitydragon.org/latest/game/assets/ux/loadingscreen/srbackground.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0, 0);
  /* 투명도를 조절하여 밝기 조절 가능 */
  z-index: -1;
  /* 오버레이를 배경 이미지 뒤로 보냄 */
}

code {
  font-family: 'KBO-Dia-Gothic_bold', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace, sans-serif;
}